"use client";

import { useState } from "react";

export default function ForgotPasswordPage() {
  const [email, setEmail] = useState("");
  const [loading, setLoading] = useState(false);
  const [success, setSuccess] = useState(false);
  const [error, setError] = useState("");

  async function handleSubmit(e: React.FormEvent) {
    e.preventDefault();
    setLoading(true);
    setError("");

    const res = await fetch("/api/auth/forgot-password", {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({ email }),
    });

    const data = await res.json();

    if (!res.ok) {
      setError(data.error || "Erro ao enviar email.");
      setLoading(false);
      return;
    }

    setSuccess(true);
    setLoading(false);
  }

  return (
    <div className="min-h-screen w-full flex items-center justify-center bg-black px-4">
      <div className="w-full max-w-md rounded-2xl bg-zinc-900 p-8 shadow-xl">
        <h1 className="text-2xl font-semibold text-white mb-2">
          Recuperar senha
        </h1>

        <p className="text-sm text-zinc-400 mb-6">
          Informe seu email para receber o link de redefinição.
        </p>

        {success ? (
          <p className="text-sm text-emerald-400">
            Se o email existir, enviamos um link para redefinir sua senha.
          </p>
        ) : (
          <form onSubmit={handleSubmit} className="space-y-4">
            <input
              type="email"
              placeholder="Seu email"
              required
              value={email}
              onChange={(e) => setEmail(e.target.value)}
              className="
                w-full rounded-lg bg-zinc-800 border border-zinc-700
                px-4 py-3 text-white placeholder-zinc-500
                focus:outline-none focus:ring-2 focus:ring-emerald-500
              "
            />

            {error && (
              <p className="text-sm text-red-400">{error}</p>
            )}

            <button
              disabled={loading}
              className="
                w-full rounded-lg bg-emerald-500 py-3
                font-medium text-black transition
                hover:bg-emerald-400 disabled:opacity-60
              "
            >
              {loading ? "Enviando..." : "Enviar link"}
            </button>
          </form>
        )}

        <div className="mt-6 text-center">
          <a
            href="/login"
            className="text-sm text-zinc-400 hover:text-white transition"
          >
            Voltar para login
          </a>
        </div>
      </div>
    </div>
  );
}
